.ail-icon-menu {
  --icon-menu-width: 2rem;
  --icon-menu-color: white;

  font-size: var(--icon-menu-width);
  width: 1em;
  height: .75em;
  position: relative;
  -webkit-transform: rotate(0deg);
  transform: rotate(0deg);
  transition: .5s ease-in-out;
  cursor: pointer;
}

.ail-icon-menu span {
  display: block;
  position: absolute;
  height: .06em;
  width: 50%;
  background: var(--icon-menu-color);
  opacity: 1;
  -webkit-transform: rotate(0deg);
  transform: rotate(0deg);
  transition: .25s ease-in-out;
}

.ail-icon-menu span:nth-child(even) {
  left: 48%;
}

.ail-icon-menu span:nth-child(odd) {
  left: 0;
}

.ail-icon-menu span:nth-child(1),
.ail-icon-menu span:nth-child(2) {
  top: 0;
}

.ail-icon-menu span:nth-child(3),
.ail-icon-menu span:nth-child(4) {
  top: .3em;
}

.ail-icon-menu span:nth-child(5),
.ail-icon-menu span:nth-child(6) {
  top: .6em;
}

.ail-icon-menu.open span:nth-child(1),
.ail-icon-menu.open span:nth-child(6) {
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
}

.ail-icon-menu.open span:nth-child(2),
.ail-icon-menu.open span:nth-child(5) {
  -webkit-transform: rotate(-45deg);
  transform: rotate(-45deg);
}

.ail-icon-menu.open span:nth-child(1) {
  left: .083em;
  top: .15em;
}

.ail-icon-menu.open span:nth-child(2) {
  left: calc(50% - 0.09em);
  top: .15em;
}

.ail-icon-menu.open span:nth-child(3) {
  left: -50%;
  opacity: 0;
}

.ail-icon-menu.open span:nth-child(4) {
  left: 100%;
  opacity: 0;
}

.ail-icon-menu.open span:nth-child(5) {
  left: .083em;
  top: 0.48em;
}

.ail-icon-menu.open span:nth-child(6) {
  left: calc(50% - 0.09em);
  top: 0.48em;
}
.ail-dropdown {
  position: relative;
}

.ail-dropdown .ail-dropdown-toggle {
  cursor: pointer;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.ail-dropdown > input[type=checkbox],
.ail-dropdown .ail-dropdown-content {
  display: none;
}

.ail-dropdown input[type=checkbox]:checked ~ .ail-dropdown-content {
  display: block;
  position: absolute;
}

.ail-dropdown--floating input[type=checkbox]:checked ~ .ail-dropdown-content {
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
  left: 100%;
}
.ail-btn {
  border: none;
  border: 1px solid transparent;
  background: transparent;
  border-radius: .3rem;
  padding: 0.4rem 1rem;
  cursor: pointer;
  text-align: center;
  font-size: 1rem;
}

.ail-btn:hover {
  opacity: .95;
}

/* Default */

.ail-btn--default {
  color: var(--bg-default);
  border-color: var(--bg-default);
  transition: .1s ease-in all;
}

.ail-btn--default:hover {
  color: var(--bg-default-alt);
  border-color: var(--bg-default);
  background: var(--bg-default);
}

/* Primary */

.ail-btn--primary {
  color: var(--bg-primary);
  border-color: var(--bg-primary);
  transition: .1s ease-in all;
}

.ail-btn--primary:hover {
  color: var(--bg-primary-alt);
  border-color: var(--bg-primary);
  background: var(--bg-primary);
}

/* Success */

.ail-btn--success {
  color: var(--bg-success);
  border-color: var(--bg-success);
  transition: .1s ease-in all;
}

.ail-btn--success:hover {
  color: var(--bg-success-alt);
  border-color: var(--bg-success);
  background: var(--bg-success);
}

/* Disabled */

.ail-btn.disabled,
.ail-btn[disabled] {
  opacity: .8;
  pointer-events: none;
}

/* Button list */

.ail-btn-list {
  list-style: none;
  padding: 0;
  margin: 0;
}

.ail-btn-list .ail-btn {
  border: none;
  border-radius: 0;
  color: var(--bg-default-alt);
  background: var(--bg-default);
}

.ail-toggle .ail-btn {
  background: var(--bg-danger);
  color: var(--bg-danger-alt);
  display: block;
}

.ail-toggle--on .ail-btn,
.ail-toggle input[type=checkbox]:checked ~ .ail-btn {
  background: var(--bg-success);
  color: var(--bg-success-alt);
}

.ail-input {
  padding: .5rem;
  width: 100%;
  box-sizing: border-box;
  border: none;
  border-bottom: 1px solid;
  font-size: 1rem;
}

.ail-input[disabled] {
  font-style: italic;
}

body, #root {
  --bg-default: #f8f9fa;
  --bg-default-alt: #212529;

  --bg-primary: #cfb0ff;
  --bg-primary-alt: #212529;

  --bg-success: #52af54;
  --bg-success-alt: #ececec;

  --bg-danger: #e14e4e;
  --bg-danger-alt: white;

  --bg-darkest: #222;
  --bg-dark: #363837;
  --bg-secondary: #666;
  --border-dark: rgba(102, 102, 102, 0.3);

  margin: 0;
  padding: 0;
  font-family: sans-serif;
  height: 100vh;
  width: 100vw;
  overflow-x: hidden;
}

@media screen and (max-width: 767px) {
  body {
    font-size: 14px;
  }
}
.ait {
  background-color: var(--bg-darkest);
  color: white;
  height: 100%;
  width: 100%;
  display: flex;
}

.ail-container {
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
}

.ail-body {
  flex: 1 1 auto;
  padding: 0 4rem 1rem;
  overflow-y: auto;
  height: 100%;
}

.ail-header,
.ail-footer {
  flex: 1 0 auto;
}

@media screen and (max-width: 767px) {
  .ail-body {
    padding: 0 1rem;
  }
}

.ail-footer {
  display: flex;
}
.ail-header {
  font-size: 1.2rem;
  padding: 1.3rem 4rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid var(--border-dark);
  position: relative;
}

.ail-header-title {
  color: white;
  text-decoration: none;
  display: flex;
  align-items: baseline;
}

.ail-header-app_name {
  position: absolute;
  right: 50%;
  -webkit-transform: translateX(50%);
          transform: translateX(50%);
  bottom: -0.6rem;
  background: var(--bg-secondary);
  color: var(--bg-default);
  padding: .1rem .8rem;
  border-radius: 11px;
  font-size: .9rem;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 80%;
  white-space: nowrap;
  transition: opacity ease-in .3s;
}

@media screen and (max-width: 767px) {
  .ail-header {
    padding: 1.3rem 1rem;
  }
  .ail-body {
    padding: 0 1rem;
  }
}

/* Title icon */
@-webkit-keyframes bulb {
  0% {
    background-color: transparent;
    box-shadow: 0;
    opacity: 0;
  }
  75% {
    box-shadow: 0px 0px 20px 20px yellow;
    background-color: yellow;
    opacity: .2;
  }
  100% {
    background-color: transparent;
    box-shadow: 0;
    opacity: 0;
  }
}
@keyframes bulb {
  0% {
    background-color: transparent;
    box-shadow: 0;
    opacity: 0;
  }
  75% {
    box-shadow: 0px 0px 20px 20px yellow;
    background-color: yellow;
    opacity: .2;
  }
  100% {
    background-color: transparent;
    box-shadow: 0;
    opacity: 0;
  }
}

.ail-header-title_icon {
  position: relative;
  -webkit-transform: rotate(0deg);
          transform: rotate(0deg);
}

.ail--sidebar-open .ail-header .ail-header-title_icon {
  -webkit-transform: translateY(0.5rem) rotate(180deg);
          transform: translateY(0.5rem) rotate(180deg);
}

@media screen and (max-width: 991px) {
  .ail--sidebar-open .ail-header .ail-header-title_icon {
    -webkit-transform: translateY(3rem) rotate(180deg);
            transform: translateY(3rem) rotate(180deg);
  }
}

.ail-header .ail-header-title_icon::before,
.ail-header .ail-header-title_icon::after {
  transition: opacity .5s ease-in;
  transition-delay: .1s;
  opacity: 0;
  height: 0;
  width: 0;
  content: ' ';
}

.ail--sidebar-open .ail-header .ail-header-title_icon::before {
  content: ' ';
  border-right: 1px white solid;
  position: absolute;
  height: 7rem;
  width: auto;
  -webkit-transform: translateX(50%);
          transform: translateX(50%);
  right: 50%;
  top: 1rem;
  opacity: 1;
}

.ail--sidebar-open .ail-header .ail-header-title_icon::after,
.ail-header-title_icon--loading::after {
  content: ' ';
  opacity: .4;
  border-radius: 50%;
  position: absolute;
  -webkit-transform: translateX(50%);
          transform: translateX(50%);
  bottom: 20%;
  right: 50%;
  height: 1rem;
  width: 1rem;
  z-index: 100;
  -webkit-animation: bulb 2s infinite cubic-bezier(0.55, 0.06, 0.68, 0.19);
          animation: bulb 2s infinite cubic-bezier(0.55, 0.06, 0.68, 0.19);
}
.ail-log {
  font-family: 'Courier New', Courier, monospace;
}

.ail-log_line {
  display: flex;
}

@media screen and (max-width: 767px) {
  .ail-log_line {
      flex-direction: column;
      padding-bottom: 1rem;
  }
}

.ail-log_line-time {
  padding-right: 1rem;
  color: lime;
  flex: 0 1 auto;
  white-space: nowrap;
}

.ail-log_line-message {
  flex: 1 0;
  white-space: pre-line;
  word-break: break-all;
}

.ail-log_line--error .ail-log_line-message,
.ail-log_line--critical .ail-log_line-message {
  color: red;
}

.ail-log_line--warning .ail-log_line-message {
  color: yellow;
}

.ail-log_line--verbose .ail-log_line-message {
  color: lime;
  font-style: italic;
}
.ail-sidebar {
  width: 0;
  visibility: hidden;
  opacity: 0;
}

.ail--sidebar-open .ail-sidebar {
  visibility: visible;
  opacity: 1;
  transition: .2s width cubic-bezier(0.48, -0.07, 0.56, 0.7), .2s opacity cubic-bezier(0.48, -0.07, 0.56, 0.7);
  flex: 0 0 auto;
  width: 20rem;
  padding: 1rem;
  box-shadow: inset 3px 1px 14px 0px rgba(0, 0, 0, 0.32);
  background: var(--bg-secondary);
  overflow-y: auto;
}

@media screen and (max-width: 991px) {
  .ail--sidebar-open .ail-header-title {
    -webkit-transform: rotate(-90deg);
            transform: rotate(-90deg);
  }

  .ail--sidebar-open .ail-sidebar {
    flex: 0 1 90%;
  }

  .ail--sidebar-open .ail-header {
    padding: 0;
  }

  .ail--sidebar-open .ail-container {
    flex: 1 0;
  }

  .ail--sidebar-open .ail-body,
  .ail--sidebar-open .ail-footer,
  .ail--sidebar-open .ail-footer-status,
  .ail--sidebar-open .ail-header-app_name {
    display: none;
  }
  .ail--sidebar-open .ail-credentials-menu {
    position: fixed;
    top: 1.3rem;
    left: 1rem;
  }
}

@media screen and (max-width: 767px) {
  .ail--sidebar-open .ail-sidebar {
    flex: 0 1 95%;
  }

  .ail--sidebar-open .ail-header-title {
    visibility: hidden;
    width: 4rem;
  }
}

.ail-credentials-menu .ail-dropdown-content {
  background: var(--bg-dark);
  border-radius: 3px;
  min-width: 18rem;
  padding: .5rem;
}

.ail-credentials-section>label {
  padding-top: .5rem;
  padding-bottom: .2rem;
  display: block;
}

@media screen and (max-width: 480px) {
  .ail-credentials-menu .ail-dropdown-toggle {
    text-align: center;
    width: 100%;
    display: block;
  }
  .ail-credentials-menu.ail-dropdown input[type=checkbox]:checked~.ail-dropdown-content {
    left: 0;
    -webkit-transform: none;
            transform: none;
    width: auto;
    position: relative;
  }
}
.ail-query {
  border: none;
  padding: 1rem;
  height: 2rem;
  width: 100%;
  resize: none;
}

.ail-search {
  max-width: 10rem;
  width: 20%;
  border: none;
  background: var(--bg-secondary);
  font-size: 1.2rem;
  color: white;
  cursor: pointer;
}

.ail-search:hover {
  opacity: .95;
}

.ail-search:disabled {
  opacity: .5;
  cursor: default;
}
.ail-footer-status {
  flex-flow: column;
  font-family: 'Courier New', Courier, monospace;
  font-size: .8rem;
  border-top: 1px solid var(--border-dark);
  display: flex;
  justify-content: space-between;
}

.ail-footer-status-item {
  padding: 0 0.5rem;
}

.ail-footer-status--timestamp {
  background: green;
  flex: 1 0 auto;
}

.ail-footer-status--error {
  background: red;
  font-style: italic;
  padding: .5rem;
}
